import { createAsyncThunk } from "@reduxjs/toolkit";
import { getAllproductsApi, getMenApi, getWomenApi,getProductdetailApi, addProductToCartApi } from "./api";

export const getAllproducts = createAsyncThunk(
  "getAllproductsApi/ AllproductsApi",
  async () => {
    const res = await getAllproductsApi();

    return res;
  }
);
export const getmenproducts = createAsyncThunk(
  "getmenproductsApi/ menproductsApi",
  async () => {
    const res = await getMenApi();

    return res;
  }
);
export const getWomenproducts = createAsyncThunk(
  "getWomenproductsApi/ WomenproductsApi",
  async () => {
    const res = await getWomenApi();
    console.log(res, "wwww");
    return res;
  }
);
export const getProductDetail = createAsyncThunk(
  "getProductdetailApi/ ProductdetailAp",
  async (id) => {
    const res = await getProductdetailApi(id);
    console.log(res, "PRODUCT DETAILLL");
    return res;
  }
);
export const addProductToCart = createAsyncThunk(
  'cart/addProduct',
  async (product) => {
    const response = await addProductToCartApi(product);
    console.log(response, 'ADDED TO CART');
    return response;
  }
);