import { createSlice } from "@reduxjs/toolkit";
import {
  addProductToCart,
  getAllproducts,
  getProductDetail,
  getWomenproducts,
  getmenproducts,
} from "./products.action";

const initialState = {
  //all products
  areallproductLoading: false,
  areallproductLoaded: false,
  areallproductLoadingError: false,
  getproducts: [],
  /// MENS
  menproductsLoading: false,
  menproductsLoaded: false,
  menproductsLoadedFailed: false,
  menproducts: [],
  //women product
  womenproductsLoading: false,
  womenproductsLoaded: false,
  womenproductsLoadedFailed: false,
  womenproducts: [],
  //Product Detail
  productdetailLoading: false,
  productdetailLoaded: false,
  productdetailLoadedFailed: false,
  productdetail: [],

  //add to cart product
  cartItems: [],
  cartLoading: false,
  cartLoaded: false,
  cartLoadedFailed: false,

};

const Products = createSlice({
  name: "products",
  initialState: initialState,
  extraReducers: {
    //allProduct
    [getAllproducts.pending]: (state) => {
      state.areallproductLoading = true;
    },
    [getAllproducts.fulfilled]: (state, action) => {
      state.areallproductLoading = false;
      state.areallproductLoaded = true;
      state.getproducts = action.payload;
    },
    [getAllproducts.rejected]: (state) => {
      state.areallproductLoading = false;
      state.areallproductLoadingError = true;
    },
    //men products
    [getmenproducts.pending]: (state) => {
      state.menproductsLoading = true;
    },
    [getmenproducts.fulfilled]: (state, action) => {
      state.menproductsLoading = false;
      state.menproductsLoaded = true;
      state.menproducts = action.payload;
    },
    [getmenproducts.rejected]: (state) => {
      state.menproductsLoading = false;
      state.menproductsLoadedFailed = true;
    },
    // women
    [getWomenproducts.rejected]: (state) => {
      state.areallproductLoading = false;
      state.areallproductLoadingError = true;
    },
    //men products
    [getWomenproducts.pending]: (state) => {
      state.womenproductsLoading = true;
    },
    [getWomenproducts.fulfilled]: (state, action) => {
      state.womenproductsLoading = false;
      state.womenproductsLoaded = true;
      state.womenproducts = action.payload;
    },
    [getWomenproducts.rejected]: (state) => {
      state.womenproductsLoading = false;
      state.womenproductsLoadedFailed = true;
    },

    //Product details

    [getProductDetail.pending]: (state) => {
      state. productdetailLoading = true;
    },
    [getProductDetail.fulfilled]: (state, action) => {
      state. productdetailLoading = false;
      state. productdetailLoaded = true;
      state. productdetail = action.payload;
    },
    [getProductDetail.rejected]: (state) => {
      state.productdetailLoading = false;
      state.productdetailLoadedFailed = true;
    },

    //Product add to cart
    [addProductToCart.pending]: (state) => {
      state. cartLoading = true;
    },
    [addProductToCart.fulfilled]: (state, action) => {
      state. cartLoading = false;
      state. cartLoaded = true;
      state. cartItems = action.payload;
    },
    [addProductToCart.rejected]: (state) => {
      state.cartLoading = false;
      state.cartLoadedFailed = true;
    },
  },
});

export default Products.reducer;
