import { ClientURL } from "../../http/config";

export const getAllproductsApi = async () => {
  const res = await ClientURL.get("/product/all-products ");

  return res.data;
};

//mens product
export const getMenApi = async () => {
  const res = await ClientURL.get("/product/men-products");

  return res.data;
};

//women product
export const getWomenApi = async () => {
  const res = await ClientURL.get("/product/women-products");

  return res.data;
};

export const getProductdetailApi = async (id) => {
  const url = `/product/product-detail/${id}`;
  const res = await ClientURL.get(url);
  return res.data;
};



export const addProductToCartApi = async (id) => {
  const url = "/cart/add-to-cart";
  const res = await ClientURL.post(url);
  return res.data;
};